.profile__profession {
    grid-area: profession;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    margin: 0;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

@media screen and (max-width: 960px) {
    .profile__profession {
        font-size: 16px;
        line-height: 17px;
        text-align: center;
        margin-left: 50px;
    }
}

@media screen and (max-width: 380px) {
    .profile__profession {
        font-size: 14px;
        margin: 0 auto;
    }
}